.container {
  max-width: 1200px;
  margin: 0 auto;
 }  //overflow: hidden

.content-sticky {
  position: sticky;
  top: $gutter-xl; }

.hide-sm {
  display: inline-block !important;
  @include media(sm) {
    display: none !important; } }

.show-sm {
  display: none !important;
  @include media(sm) {
    display: inline-block !important; } }

.has-hover {
  position: relative;
  &:after {
    position: absolute;
    content: "";
    width: 100%;
    height: 2px;
    top: 110%;
    left: 0;
    background: $color-text-alt;
    transition: transform 0.5s ease-in;
    transform: scaleX(0);
    transform-origin: right; }
  &:hover:after {
    transform: scale(1);
    transform-origin: left; } }
