.aspectratio-16-9 {
  float: left;
  display: block;
  padding-top: 56.25%;
  width: 0;
}

.aspectratio-4-3 {
  float: left;
  display: block;
  padding-top: 133.33333%;
  width: 0;
}

.aspectratio-3-2 {
  float: left;
  display: block;
  padding-top: 66.66667%;
  width: 0;
}

.aspectratio-1-1 {
  float: left;
  display: block;
  padding-top: 100%;
  width: 0;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

*::after, *::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
}

@media (min-width: 112.5em) {
  html {
    font-size: 75%;
  }
}

@media (max-width: 75em) {
  html {
    font-size: 56.25%;
  }
}

@media (max-width: 56.25em) {
  html {
    font-size: 50%;
  }
}

@media (max-width: 37.5em) {
  html {
    font-size: 45%;
  }
}

body {
  box-sizing: border-box;
  overflow-x: hidden;
}

a {
  text-decoration: none;
  font-style: normal;
}

img, picture {
  display: block;
  width: 100%;
  height: 100%;
}

::selection {
  background-color: #3E3B4B;
  color: #fff;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

.content-sticky {
  position: sticky;
  top: 8rem;
}

.hide-sm {
  display: inline-block !important;
}

@media (max-width: 37.5em) {
  .hide-sm {
    display: none !important;
  }
}

.show-sm {
  display: none !important;
}

@media (max-width: 37.5em) {
  .show-sm {
    display: inline-block !important;
  }
}

.has-hover {
  position: relative;
}

.has-hover:after {
  position: absolute;
  content: "";
  width: 100%;
  height: 2px;
  top: 110%;
  left: 0;
  background: #d6b360;
  transition: transform 0.5s ease-in;
  transform: scaleX(0);
  transform-origin: right;
}

.has-hover:hover:after {
  transform: scale(1);
  transform-origin: left;
}

body {
  font-size: 1.6rem;
  line-height: 1.6;
  font-family: europa, sans-serif;
  font-weight: 400;
}

.heading-primary,
.heading-secondary,
.heading-tertiary,
.paragraph,
.navigation-primary__item {
  font-style: normal;
  color: #2a2738;
}

.theme-dark .heading-primary, .theme-dark
.heading-secondary, .theme-dark
.heading-tertiary, .theme-dark
.paragraph, .theme-dark
.navigation-primary__item {
  color: #fff;
}

.theme-dark .heading-primary--highlighted, .theme-dark
.heading-secondary--highlighted, .theme-dark
.heading-tertiary--highlighted, .theme-dark
.paragraph--highlighted, .theme-dark
.navigation-primary__item--highlighted {
  color: #D7B560;
}

.heading-primary {
  font-family: itc-avant-garde-gothic-pro, sans-serif;
  font-size: 6rem;
  line-height: 1;
  margin-bottom: 4rem;
  font-weight: 700;
}

.heading-secondary {
  font-family: itc-avant-garde-gothic-pro, sans-serif;
  font-size: 3.5rem;
  line-height: 1;
  font-weight: 700;
}

.paragraph {
  font-size: 1.6rem;
  margin-bottom: 4rem;
}

.paragraph--highlighted {
  font-family: itc-avant-garde-gothic-pro, sans-serif;
  font-size: 1.7rem;
  font-weight: 300;
}

.navigation-footer__item {
  list-style: none;
  margin-bottom: 1.45rem;
}

.navigation-footer__item__link {
  color: #fff;
}

.page-header {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
  padding-right: 7rem;
  padding-left: 7rem;
}

.navigation-mobile {
  display: none;
}

@media (max-width: 37.5em) {
  .navigation-mobile {
    display: block;
  }
}

.navigation-primary {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

@media (max-width: 37.5em) {
  .navigation-primary {
    display: none;
  }
}

.navigation-primary__item {
  margin-right: 5rem;
}

.navigation-primary__cta {
  margin: 0 0 0 auto;
}

.logo__image {
  height: 40px;
  width: auto;
}

.section-featured {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

@media (max-width: 56.25em) {
  .section-featured {
    grid-template-columns: auto;
  }
}

.section-featured.theme-dark {
  background-color: #252235;
}

.section-featured__content {
  padding-top: 8rem;
  padding-bottom: 8rem;
  padding-right: 7rem;
  padding-left: 7rem;
}

.section-featured .featured-image__image {
  object-fit: cover;
}

@media (max-width: 56.25em) {
  .section-featured .featured-image__image {
    max-height: calc(100vh - (2 * 1.5rem));
  }
}

.section-featured--reversed {
  margin-bottom: 1.5rem;
}

@media (max-width: 56.25em) {
  .section-featured--reversed {
    grid-template-columns: auto;
  }
}

.section-featured--reversed .section-featured__content {
  grid-row: 1;
}

@media (max-width: 56.25em) {
  .section-featured--reversed .section-featured__content {
    grid-row: unset;
  }
}

.text-block__content {
  padding-left: 5rem;
}

.section-banner {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  padding-top: 10rem;
  padding-bottom: 10rem;
  padding-right: 7rem;
  padding-left: 7rem;
}

@media (max-width: 56.25em) {
  .section-banner {
    padding: 0 1.5rem 1.5rem;
  }
}

.section-banner__image {
  overflow: hidden;
  z-index: 5;
  grid-row: 1/2;
  grid-column: 1/5;
}

.section-banner__content {
  padding: 5rem;
  z-index: 10;
  grid-row: 1/2;
  grid-column: 1/3;
}

@media (max-width: 75em) {
  .section-banner__content {
    grid-column: 1/4;
  }
}

@media (max-width: 56.25em) {
  .section-banner__content {
    background-image: linear-gradient(to bottom, white 0%, transparent 228%);
    grid-column: 1/5;
  }
}

.banner-image {
  overflow: hidden;
}

.banner-image__image {
  object-fit: cover;
  max-height: calc(100vh - (2 * 8rem));
  min-height: 460px;
}

.page-footer {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  background-color: #252235;
  background-image: url("../../assets/footer-bg.png");
  background-repeat: repeat;
  background-size: 320px;
  padding-bottom: 4rem;
}

.page-footer .contact, .page-footer .navigation-footer {
  padding-top: 8rem;
  padding-bottom: 8rem;
  padding-right: 7rem;
  padding-left: 7rem;
}

@media (max-width: 37.5em) {
  .page-footer {
    grid-template-columns: auto;
  }
}

.btn {
  font-size: 1.4rem;
  font-family: itc-avant-garde-gothic-pro, sans-serif;
  font-weight: 300;
  line-height: 100%;
  border: 3px solid #D7B560;
  background-color: transparent;
  padding: 14px 28px 12px;
  border-radius: 30px;
  display: inline-block;
  color: #2a2738;
  outline: none;
  transition: all .4s ease;
}

.btn--icon {
  padding-left: 14px;
  padding-right: 14px;
}

.btn--icon .las {
  -webkit-text-stroke: 1px #252235;
  color: #252235;
}

.btn:not(:last-of-type) {
  margin-right: 1.5rem;
}

.btn:hover {
  background-color: #D7B560;
  color: #fff;
}

.btn:hover .las {
  -webkit-text-stroke: 1px #fff;
  color: #fff;
}

.theme-dark .btn {
  color: #D7B560;
}

.theme-dark .btn:hover {
  background-color: #D7B560;
  color: #fff;
}

.theme-imagery .btn {
  background-color: #D7B560;
  color: #fff;
}

.theme-imagery .btn:hover {
  background-color: #252235;
  border-color: #252235;
}

.contact {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-items: flex-start;
}

.contact__item {
  color: #fff;
  font-style: normal;
  font-family: itc-avant-garde-gothic-pro, sans-serif;
  font-size: 1.7rem;
  font-weight: 300;
  margin-bottom: 1.5rem;
}

.newsletter__input {
  height: 100%;
  border: 3px solid white;
  background: transparent;
  margin: 0;
  outline: 0;
  padding: .7rem .4rem;
  width: 21rem;
  color: #fff;
}

.newsletter__button {
  background: none;
  outline: 0;
  border: 0;
  color: white;
  position: relative;
  left: 0;
  transform: translateX(-200%);
}

.newsletter__button .las {
  -webkit-text-stroke: 1px #fff;
}

.section-featured__media--divided {
  position: relative;
}

.section-featured__media--divided:before, .section-featured__media--divided:after {
  content: "";
  background: white;
  position: absolute;
  z-index: 10;
}

.section-featured__media--divided:before {
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 1.5rem;
  height: 100%;
}

.section-featured__media--divided:after {
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 50%;
  height: 1.5rem;
}

.section-slider__images {
  z-index: 5;
}

.section-slider__caption {
  z-index: 10;
  width: 50%;
  background: white;
  height: 12rem;
  position: absolute;
  transform: translateY(-100%);
  max-width: 600px;
  min-width: 550px;
}

@media (max-width: 56.25em) {
  .section-slider__caption {
    position: unset;
    transform: none;
    max-width: unset;
    min-width: unset;
    width: 100%;
  }
}

.section-slider__caption__single {
  display: flex !important;
  justify-content: center;
  align-items: center;
  height: 12rem;
}

.section-slider__nav {
  z-index: 10;
  display: flex;
  justify-content: flex-start;
  flex-direction: row-reverse;
  position: absolute;
  align-items: center;
  width: 50%;
  transform: translate(100%, -100%);
  height: 12rem;
  padding-right: 7rem;
}

@media (max-width: 56.25em) {
  .section-slider__nav {
    padding-right: 0;
    transform: translateY(-200%);
    justify-content: center;
    flex-direction: row;
    width: 100%;
  }
}

.slick-arrow {
  cursor: pointer;
  border-color: #252235;
  margin: 0 6px;
}

@media (max-width: 56.25em) {
  .slick-arrow {
    display: none !important;
  }
}

.slick-arrow:not(:last-of-type) {
  margin-right: 0;
  background-color: transparent;
  transition: all .4s ease;
}

.slick-arrow:hover {
  background-color: #252235;
}

.slick-arrow:hover .las {
  color: #fff;
  -webkit-text-stroke: 1px #fff;
}

.slick-dots {
  list-style: none;
  max-height: 47px;
  display: flex;
  align-items: center;
  text-align: center;
  margin-right: 10px;
}

.slick-dots li {
  cursor: pointer;
  width: 0;
  height: 0;
  margin: 0 14px;
}

.slick-dots li button {
  position: relative;
  font-size: 0;
  line-height: 0;
  display: block;
  width: 20px;
  height: 20px;
  cursor: pointer;
  color: transparent;
  border: 0;
  outline: none;
  background: transparent;
  transform: translateY(-50%);
}

.slick-dots li button:before {
  content: "";
  background-color: transparent;
  border: 2px solid #252235;
  border-radius: 50%;
  display: inline-block;
  height: 16px;
  width: 16px;
  color: transparent;
  opacity: 1;
}

.slick-dots li.slick-active button:before {
  background-color: #252235;
}

.product-info {
  margin: 0 2rem;
}

.product-info__heading {
  margin-bottom: .4rem;
}

.product-actions {
  margin: 0 2rem;
}

.social-link {
  width: 50px;
  height: 50px;
  display: block;
}

.social-link--linkedin {
  background-color: #D7B560;
}

.social-link--facebook {
  background-color: #4e83ba;
}

.social-link__icon {
  color: #2a2738;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  font-size: 32px;
  transition: .2s ease-in-out;
}

.social-link__icon:hover {
  font-size: 34px;
}
